.flash-card-wrapper{position:absolute;width:100%;height:100%;background-size:cover;background-position:center;box-sizing:border-box}.flash-card-wrapper .overlay{position:absolute;top:0;left:0;width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background:rgba(255,255,255,.5);pointer-events:none;opacity:0}.flash-card-wrapper .card{border-radius:1rem;border:3px solid #cdcdcd;background-color:#fff;height:100%;padding:1rem;padding-bottom:2rem;box-sizing:border-box;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-evenly;justify-content:space-evenly;font-size:42px;font-size:5vw}@media(max-width: 320px){.flash-card-wrapper .card{font-size:16px}}@media(min-width: 840px){.flash-card-wrapper .card{font-size:42px}}.flash-card-wrapper .card button{display:block;margin:2rem auto 0 auto;border-radius:50%;width:3rem;height:3rem;font-size:1.5rem;padding:0}.flash-card-wrapper .card.front img,.flash-card-wrapper .card.front .image{width:80%;height:80%;background-size:contain;background-repeat:no-repeat;background-position:center;margin:10%}.flash-card-wrapper .card.back img,.flash-card-wrapper .card.back .image{width:40%;background-size:contain;background-repeat:no-repeat;background-position:center;margin:auto}.flash-card-wrapper .card.back .image{height:40%}.flash-card-wrapper .card.back .subtext{font-size:smaller}.flash-card-wrapper .card.card-intro{color:#6a6a6a;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.flash-card-wrapper .card.card-intro h2{margin:1rem}.flash-card-wrapper .card.card-intro .intro-description{-webkit-flex:1 1;flex:1 1;font-size:50px;font-size:4vw}@media(max-width: 150px){.flash-card-wrapper .card.card-intro .intro-description{font-size:6px}}@media(min-width: 500px){.flash-card-wrapper .card.card-intro .intro-description{font-size:20px}}.flash-card-wrapper .card.card-intro .intro-help-correct,.flash-card-wrapper .card.card-intro .intro-help-wrong{background-size:contain;position:relative}.flash-card-wrapper .card.card-intro .intro-help-correct:before,.flash-card-wrapper .card.card-intro .intro-help-wrong:before{content:"";display:block;padding-bottom:30%}.flash-card-wrapper .card.card-intro .intro-help-correct .text,.flash-card-wrapper .card.card-intro .intro-help-wrong .text{position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);font-size:50px;font-size:4vw}@media(max-width: 150px){.flash-card-wrapper .card.card-intro .intro-help-correct .text,.flash-card-wrapper .card.card-intro .intro-help-wrong .text{font-size:6px}}@media(min-width: 500px){.flash-card-wrapper .card.card-intro .intro-help-correct .text,.flash-card-wrapper .card.card-intro .intro-help-wrong .text{font-size:20px}}.flash-card-wrapper .card.card-intro .intro-help-correct{background-image:url(/flashcards-react/static/media/intro-help-correct.00e43bb0.png)}.flash-card-wrapper .card.card-intro .intro-help-correct .text{text-align:left;left:20%}.flash-card-wrapper .card.card-intro .intro-help-wrong{background-image:url(/flashcards-react/static/media/intro-help-wrong.45a2f3a0.png)}.flash-card-wrapper .card.card-intro .intro-help-wrong .text{text-align:right;right:20%}.flash-card-wrapper .react-card-flip{width:100%;height:100%}.flash-card-wrapper .react-card-front,.flash-card-wrapper .react-card-back{will-change:transform}
.progress-bar{position:relative;border:3px solid #707070;border-radius:3rem;width:100%;height:3rem;margin-bottom:5vw}.progress-bar .track{background:#91ee76;border-radius:3rem;min-width:2rem;height:100%;transition:width .25s linear}.progress-bar .icon{position:absolute;left:.5rem;top:.45rem;height:2.5rem;width:2.5rem}
.button-bar{margin-top:5vw;height:4rem;display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center;width:100%}@media(min-width: 576px){.button-bar{height:5rem}}.button-circle{width:3.5rem;height:3.5rem;padding:.5rem;border:3px solid #cdcdcd;background:#fff;border-radius:50%;box-shadow:0px .5rem .5rem #00000029;display:block}.button-circle:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.button-circle.button-small{width:3rem;height:3rem}@media(min-width: 576px){.button-circle.button-small{width:4.5rem;height:4.5rem;padding:.75rem}}@media(min-width: 576px){.button-circle{width:5rem;height:5rem;padding:.75rem}}.button-circle svg{width:100%;height:100%}.button-text{height:3.5rem;padding:.5rem;border:3px solid #cdcdcd;font-size:1rem;background:#fff;border-radius:1.75rem;box-shadow:0px .5rem .5rem #00000029;display:-webkit-flex;display:flex;white-space:nowrap;-webkit-align-items:center;align-items:center}.button-text:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.button-text span{padding:.5rem}.button-text svg{width:100%;height:100%}
.close {
  position: fixed;
  top: 16px;
  left: 16px;
  z-index: 2
}

@media (orientation: portrait) {
  .close {
    top: 32px;
  }
}

.instructions{width:100%}.instruction-page{display:none;opacity:0;transition:opacity 5s}.instruction-page.active{display:block;opacity:1}.button-prev{position:fixed;bottom:2rem;left:2rem;-webkit-transform:scaleX(-1);transform:scaleX(-1)}.button-next{position:fixed;bottom:2rem;right:2rem}
#root{text-align:center;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;overflow:hidden;background:linear-gradient(#fff, #999);min-width:100vw;min-height:100vh}.app{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;max-width:420px;margin:2rem;-webkit-flex:1 1;flex:1 1}.card-container{width:100%;position:relative;height:96vw;max-height:490px}.card h3{position:absolute;bottom:0;margin:10px;color:#fff}@-webkit-keyframes popup{0%{-webkit-transform:scale(1, 1);transform:scale(1, 1)}10%{-webkit-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1)}30%{-webkit-transform:scale(0.9, 0.9);transform:scale(0.9, 0.9)}50%{-webkit-transform:scale(1, 1);transform:scale(1, 1)}57%{-webkit-transform:scale(1, 1);transform:scale(1, 1)}64%{-webkit-transform:scale(1, 1);transform:scale(1, 1)}100%{-webkit-transform:scale(1, 1);transform:scale(1, 1)}}@keyframes popup{0%{-webkit-transform:scale(1, 1);transform:scale(1, 1)}10%{-webkit-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1)}30%{-webkit-transform:scale(0.9, 0.9);transform:scale(0.9, 0.9)}50%{-webkit-transform:scale(1, 1);transform:scale(1, 1)}57%{-webkit-transform:scale(1, 1);transform:scale(1, 1)}64%{-webkit-transform:scale(1, 1);transform:scale(1, 1)}100%{-webkit-transform:scale(1, 1);transform:scale(1, 1)}}
